/* App.css or index.css */
body {
  font-family: Arial, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-header {
  text-align: center;
  color: white;
}

.chat-history {
  width: 80%;
  margin: 20px 0;
  padding: 10px;
  max-height: 500px;
  overflow-y: auto;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
  padding: 10px;
}

.user-message, .bot-message {
  margin-bottom: 10px;
  font-size: 16px;
}

.user-message {
  text-align: left;
  color: black;  /* User's messages will be black */
}

.bot-message {
  text-align: left;
  color: black;  /* Bot's messages will also be black */
}

textarea {
  width: 80%;
  padding: 10px;
  font-size: 16px;
  margin-top: 10px;
  border-radius: 5px;
}

button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  margin-top: 10px;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}
